import { DomElementParams } from "../models/params.intarface";

export class DomElement<T extends HTMLElement>{
  private _element: T;
  public elementName: string;

  constructor(private _name: string, private _params?: DomElementParams) {
    this.elementName = this._name;

    this._element = Helper.ElementFactory<T>(this._name, this._params);
  }

  get element(): T {
    return this._element;
  }

  public addClass(...classNames: string[]): void {
    this._element.classList.add(...classNames);
  }

  public addStyles(...styles: [string, string][]): void {
    styles.forEach(attr => this._element.style[attr[0]] = attr[1]);
  }

  public addAttributes(...attrs: [string, string][]): void {
    attrs.forEach(attr => this._element[attr[0]] = attr[1]);
  }

  public addId(id: string): void {
    this._element.id = id;
  }

  public addText(innerText: string): void {
    this._element.innerText = innerText;
  }

  public addChild(child: HTMLElement): void {
    this._element.appendChild(child);
  }

  public addEvent(eventName: string, event: (event: any) => void) {
    this._element.addEventListener(eventName, event);
  }
}

class Helper {
  public static ElementFactory<T extends HTMLElement, U extends HTMLElement = HTMLElement>(elementName: string, params?: DomElementParams, ...child: U[]): T {
    let element = <T>(document.createElement(elementName));

    if (params) {
      if (params.id) {
        element.id = params.id;
      }

      if (params.attrs) {
        params.attrs.forEach(attr => element[attr[0]] = attr[1]);
      }

      if (params.styles) {
        params.styles.forEach(attr => element.style[attr[0]] = attr[1]);
      }

      if (params.classes) {
        element.classList.add(...params.classes);
      }

      if (params.innerText) {
        element.innerText = params.innerText;
      }

      if (child) {
        for (var index = 0; index < child.length; index++) {
          element.appendChild(child[index]);
        }
      }
    }

    return element;
  }
}
