/// <reference types="@types/googlemaps" />
import { Directive, ElementRef, OnInit } from '@angular/core';
import { ScriptService } from './script.service';
import { ConfigService } from './config.service';
import { MapConfig } from '../shared/models/map-config.interface';
import { DomElement } from '../shared/classes/dom-element.classes';

@Directive({
  selector: '[appGoogleMap]'
})
export class GoogleMapDirective implements OnInit {
  private _configFile: string = '/assets/map.config.json';
  private _element: HTMLInputElement;

  private _map: google.maps.Map;
  private _searchBox: google.maps.places.SearchBox;

  private _input: DomElement<HTMLInputElement>;
  private _labelButton: DomElement<HTMLDivElement>;
  private _lineButton: DomElement<HTMLDivElement>;
  private _filterButton: DomElement<HTMLDivElement>;

  constructor(private elRef: ElementRef,
    private _scriptService: ScriptService,
    private _configService: ConfigService) {
    this._element = elRef.nativeElement;
  }

  ngOnInit(): void {
    this._input = new DomElement<HTMLInputElement>('input');

    this._labelButton = new DomElement<HTMLDivElement>('div', {
      attrs: [['title', 'Поставить метку']],
      styles: [['z-index', '1'], ['padding', '10px'], ['background-color', 'green']]
    });

    this._labelButton.addChild(new DomElement<HTMLElement>('i', {
      classes: ['fa', 'fa-map-marker'],
      attrs: [['aria-hidden', 'true']]
    }).element);

    this._lineButton = new DomElement<HTMLDivElement>('div', {
      attrs: [['title', 'Выделить участок']],
      styles: [['z-index', '1'], ['padding', '10px'], ['background-color', 'green']]
    });

    this._lineButton.addChild(new DomElement<HTMLElement>('i', {
      classes: ['fa', 'fa-arrows-h'],
      attrs: [['aria-hidden', 'true']]
    }).element);

    this._filterButton = new DomElement<HTMLDivElement>('div', {
      attrs: [['title', 'Фильтр']],
      styles: [['z-index', '1'], ['padding', '10px'], ['background-color', 'green']]
    });

    this._filterButton.addChild(new DomElement<HTMLElement>('i', {
      classes: ['fa', 'fa-filter'],
      attrs: [['aria-hidden', 'true']]
    }).element);


    this._configService.load(this._configFile).subscribe(resolve => {
      const mapConfig = resolve.json() as MapConfig;

      this.loadMapScript(mapConfig.api.uri);
    })
  }

  private loadMapScript(uri: string): void {
    this._scriptService.load(uri).subscribe(
      () => {
        this.initialMap();

        this.initialSearchBox();

        this._map.controls[google.maps.ControlPosition.LEFT_CENTER].push(this._labelButton.element);

        this._map.controls[google.maps.ControlPosition.LEFT_CENTER].push(this._lineButton.element);

        this._map.controls[google.maps.ControlPosition.RIGHT_CENTER].push(this._filterButton.element);

        this.initialEvents()
      },
      reject => {
        console.log(reject);
      });
  }

  private initialMap(): void {
    const options = {
      zoom: 13,
      center: {
        lat: 53.348053,
        lng: 83.779875
      },
      mapTypeControl: true,
      mapTypeControlOptions: {
        mapTypeIds: [
          google.maps.MapTypeId.ROADMAP,
          google.maps.MapTypeId.SATELLITE,
          google.maps.MapTypeId.HYBRID,
          google.maps.MapTypeId.TERRAIN
        ],
        style: google.maps.MapTypeControlStyle.DROPDOWN_MENU,
        position: google.maps.ControlPosition.LEFT_TOP
      },
      zoomControl: true,
      zoomControlOptions: {
        position: google.maps.ControlPosition.RIGHT_TOP
      },
      scaleControl: false,
      streetViewControl: false,

      mapTypeId: google.maps.MapTypeId.ROADMAP
    };

    this._map = new google.maps.Map(this._element, options);
  }

  private initialSearchBox(): void {
    this._searchBox = new google.maps.places.SearchBox(this._input.element);

    this._map.controls[google.maps.ControlPosition.TOP_LEFT].push(this._input.element);
  }

  private initialEvents(): void {
    this._map.addListener('bounds_changed', () => {
      this._searchBox.setBounds(this._map.getBounds());
    });

    this._map.addListener('click', (e) => {
      this.placeMarkerAndPanTo(e.latLng);
    });

    this._searchBox.addListener('places_changed', () => {
      let places = this._searchBox.getPlaces();

      this._map.setCenter(places[0].geometry.location);
    });

    this._labelButton.addEvent('click', () => console.log('_labelButton'));

    this._lineButton.addEvent('click', () => console.log('_lineButton'));

    this._filterButton.addEvent('click', () => console.log('_filterButton'));

    this._input.addEvent('input', event => console.log(event.target.value));
  }

  private placeMarkerAndPanTo(latLng) {
    let marker = new google.maps.Marker({
      position: latLng,
      map: this._map
    });

    this._map.panTo(latLng);
  }
}
